<template>
    <div class="users-management-container">
      <div class="all-confirmed-users">
        <h2 class="workers-title">Працівники</h2>
        <div class="workers-container">
          <div class="control-menu-container">
            
            <div class="custom-select" >
              <div class="selected-option" @click="toggleOpenCloseUserRoleSelect('selected-search')" :class="searchRoleSelectIsOpen ? 'selected-option-is-active' : ''"><span>{{selectedSearchRole}}</span><img src="../assets/images/dropdown-icon.png" :class="searchRoleSelectIsOpen ? 'dropdown-active-icon' : ''" alt=""></div>
              <div class="options-container" v-if="selectUserRoleIsOpen == 'selected-search'">
                <div class="option" v-if="selectedSearchRole!='Усі працівники'" @click="displayAllRoles('selected-search')">Усі працівники</div>

                <template v-for="role in allUserRoles" >
                  <div class="option" v-if="selectedSearchRole != displaySearchRole(role)" :key="role" @click="setSelectedSearchRole('selected-search', role)">{{ displaySearchRole(role) }}</div>
                </template>
                <div class="option" v-if="selectedSearchRole!='Очікують підтвердження'" @click="setSelectedSearchRole('selected-search', role)">Очікують підтвердження</div>
              </div>
            </div>
            <input type="text" class="find-user-input" placeholder="Пошук..." v-model="searchQuery">
          </div>
          <div class="filtered-users-container">
            <template v-for="user in filteredUsers">
              <div class="user-card" :key="user.uid" v-if="(selectedSearchRole == 'Усі працівники') || (displaySearchRole(user.role) == selectedSearchRole)">
                <h2>{{ user.lastname ? user.lastname + " " : ''}}{{user.firstname}}{{ user.surname ? " " + user.surname : ''}}</h2>
                  

                <ul>
                  <li>Пошта: {{ user.email }}</li>
                  <li>Номер: {{ user.phoneNumber ? user.phoneNumber : 'Не вказаний'}}</li>
                  <div class="user-role-field">
                      <span>Посада:</span>  
                      <div class="custom-select">
                        <div class="selected-option" @click="toggleOpenCloseUserRoleSelect(user.uid)" :class="(selectUserRoleIsOpen == user.uid) ? 'selected-option-is-active' : ''"><span>{{displayRole(selectedUserRole[user.uid].role)}}</span><img src="../assets/images/dropdown-icon.png" :class="selectUserRoleIsOpen==user.uid ? 'dropdown-active-icon' : ''" alt=""></div>
                        <div class="options-container" v-if="selectUserRoleIsOpen == user.uid">
                          <template v-for="role in allUserRoles" >
                            <div class="option" v-if="selectedUserRole[user.uid].role != role" :key="role" @click="setSelectedUserRole(user.uid, role)">{{ displayRole(role) }}</div>
                          </template>
                          <div class="option" v-if="selectedUserRole[user.uid].role"  @click="setSelectedUserRole(user.uid, '')">Очікує підтвердження</div>
                        </div>
                      </div>
                      <button v-if="selectedUserRole[user.uid] &&  (user.role != selectedUserRole[user.uid]?.role)" @click="updateUserRole(user.uid, selectedUserRole[user.uid].role)">Зберегти</button>
                  </div>
                </ul>
              </div>
              
            </template>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
export default {
name: 'UsersManagement',

props: {
  
},

data() {
    return {
        allUsers: [],
        allUserRoles: [],
        searchRoleSelectIsOpen: false,
        selectedSearchRole: 'Усі працівники',
        selectUserRoleIsOpen: '',
        selectedUserRole: {},
        searchQuery: '',
    };
  },

  methods: {
    displayRole(role){
      switch (role) {
        case 'admin':
          return 'Адміністратор';
        case 'manager':
          return 'Менеджер';
        case 'repairman':
          return 'Ремонтник';
        case 'coordinator':
          return 'Координатор';
        case 'driver':
          return 'Водій';
        default:
          return 'Очікує підтвердження';
      }
    },
    displaySearchRole(role){
      switch (role) {
        case 'admin':
          return 'Адміністратори';
        case 'manager':
          return 'Менеджери';
        case 'repairman':
          return 'Ремонтники';
        case 'coordinator':
          return 'Координатори';
        case 'driver':
          return 'Водії';
        default:
          return 'Очікують підтвердження';
      }
    },
    setSelectedSearchRole(uid, role){
      this.selectedSearchRole = this.displaySearchRole(role);
      this.toggleOpenCloseUserRoleSelect(uid);
    },
    toggleOpenCloseUserRoleSelect(uid){
      if(this.selectUserRoleIsOpen == uid){
        this.selectUserRoleIsOpen = '';
      }
      else{
        this.selectUserRoleIsOpen = uid;
      }
    },
    setSelectedUserRole(uid, role) {
      this.selectedUserRole[uid].role = role;
      this.toggleOpenCloseUserRoleSelect(uid);
    },
    displayAllRoles(uid){
      this.selectedSearchRole = 'Усі працівники';
      this.toggleOpenCloseUserRoleSelect(uid);
    },
    updateUserRole(uid, role){
      const userIndex = this.allUsers.findIndex(user => user.uid === uid);
  
      if (userIndex !== -1) {
        this.allUsers[userIndex].role = role;
      } else {
        console.error(`Користувач с UID ${uid} не знайдений.`);
      }
      
      return this.$store.dispatch('updateUserRole', {uid, role});
    }
  },
  
  computed: {
    filteredUsers() {
      if(this.searchQuery){
        // Фильтрация пользователей в соответствии с поисковым запросом
        return this.allUsers.filter(user => {
          const lowerSearchQuery = this.searchQuery.toLowerCase();

          // Проверка наличия поискового запроса в любом из полей пользователя
          return Object.values(user).some(value =>
            String(value).toLowerCase().includes(lowerSearchQuery)
          );
        });
      }else{
        return this.allUsers;
      }
      
    },
  },

  mounted() { 
    this.$store.dispatch('getAllUsers').then((querySnapshot)=>{
      querySnapshot.forEach((result) => {
        var user = result.data();
        this.allUsers.push(user);
        this.selectedUserRole[user.uid] = {role: user.role}
      });
    });
    this.$store.dispatch('getAllUserRoles').then((querySnapshot)=>{
      querySnapshot.forEach((result) => {
        this.allUserRoles.push(result.id);
      });
    });
    
  }
}
</script>